@use 'src/assets/scss/index.scss';

.primaryCard {
    display: flex;
    flex-direction: column;
    padding: 20px;
    margin: 15px;
    cursor: pointer;
    border: solid 2px index.$bannerLineColor;
    transition: all ease-in-out 0.3s;
    width: 19%;
    height: 300px;
    border-radius: 10px;
    .cardTitle {
        @include index.fontProps(22px, 26px, index.$textColor);
        margin: 15px 0;
        letter-spacing: 2px;
    }
    &:hover {
        background-color: index.$primaryCardClor;
        transition: all ease-in-out 0.3s;
    }
}